import './App.css';
import Navbar from './componets/Navbar';
import Textform from './componets/Textform';
import About from './componets/About';
import Start from './componets/Start';
import {
  createBrowserRouter,
  RouterProvider,
} from "react-router-dom";


const router = createBrowserRouter(
([
  {
    path: "/",
    element: <><Navbar title="TextWizz..."/><Start/></>,
  },
  {
    path: "/Textform",
    element: <><Navbar title="TextWizz..." /><Textform /></>,
  },
  {
    path: "/About",
    element: <><Navbar title="TextWizz..." /><About /></>,
  },
]
),
{
  basename: "/TextWizz..."
}

);

function App() {

  return (
     <>
          <RouterProvider router={router} />
     </>
  );
}
export default App;
