import React from 'react'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGithub } from '@fortawesome/free-brands-svg-icons'
import { faLinkedin } from '@fortawesome/free-brands-svg-icons'



export default function Navbar(props) {


  return (
    <>

<nav className="navbar navbar-expand-lg bg-body-tertiary">
  <div className="container-fluid">
    <Link className="navbar-brand" to="/">
      {props.title}
    </Link>
    <button
      className="navbar-toggler"
      type="button"
      data-bs-toggle="collapse"
      data-bs-target="#navbarSupportedContent"
      aria-controls="navbarSupportedContent"
      aria-expanded="false"
      aria-label="Toggle navigation"
    >
      <span className="navbar-toggler-icon" />
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
       
          <Link className="nav-link active" aria-current="page" to="/Textform">
          <li className="nav-item">
            Home
            </li>
          </Link>
     
         <Link className="nav-link" to="/About">
        <li className="nav-item">
            About us
        </li>
          </Link>
      </ul>
      <div className="link d-flex">
        <a  className="mx-4 nav-link" href="https://www.linkedin.com/in/m-bhavishya-jain" target='_blank' rel="noreferrer"> <FontAwesomeIcon icon={faLinkedin} /></a>
    
       <a  className="mx-4 nav-link" href="https://github.com/Bhavishya-Obani" target='_blank' rel="noreferrer">  <FontAwesomeIcon icon={faGithub}/></a>
        
      </div>
      
      
    </div>
  </div>
</nav>



    </>
  )
}

