import React ,{useState} from 'react';
import { jsPDF } from "jspdf"


export default function Textform() {

    const [text , settext] = useState("")
    const [undo, setUndo] = useState([]);
    const [redo, setRedo] = useState([]);

       //functions
      
    const Export = () =>{
  const doc = new jsPDF();
   doc.text(text , 10 ,10);
   doc.save("Exported_textWizz.pdf")
   }

       const handle = (e) => {
        const newtext = e.target.value ;
        setUndo([...undo, text]);
        setRedo([]); // Clear redo stack on new input
        settext(newtext);
        }
  
        const Undo = () => {
          if (undo.length === 0) return;
      
          const lasttext = undo.pop();
          setRedo([...redo, text]); // Save current text to redo stack
          settext(lasttext);
          setUndo([...undo]); // Update the undo stack
        };
       
        const Redo = () => {
          if (redo.length === 0) return;
      
          const nexttext = redo.pop();
          setUndo([...undo, text]); // Save current text to undo stack
          settext(nexttext);
          setRedo([...redo]); // Update the redo stack
        };

      const Clear = () => {
        let newtext="";
        settext(newtext);
      }
  
      const UpperCase = () => {
          let newtext = text.toUpperCase();
          settext(newtext);
        }
  
        const LowerCase = () => {
          let newtext= text.toLowerCase();
          settext(newtext);
        }
  
        const Copy = () => {
          navigator.clipboard.writeText(text).then(()=> {
              alert("Copied..");
          })
        }
  
        const Removextra = () => {
            let newtext = text.trim().replace(/\s+/g,' ')
            settext(newtext);
          }
      
          
         
  return (
    <>

    <div>
    <h2 className=' p-3'>Text Editor....!!!!</h2>

    <div className=" p-4 mb-2">
        <textarea className="form-control" value={text} onChange={handle} id="mybox" rows="8" placeholder='Enter...'></textarea>
    </div>

<div className=' d-flex justify-content-center'>
    <button className="btn btn-outline-success m-2" onClick={Copy}>Copy</button>
    <button className="btn btn-outline-success  m-2" onClick={Clear}>Clear</button>
    <button className="btn btn-outline-success  m-2" onClick={UpperCase}>UpperCase</button>
    <button className="btn btn-outline-success  m-2" onClick={LowerCase}>LowerCase</button>
    <button className="btn btn-outline-success m-2" onClick={Removextra}>Remove Extra Spaces</button>
    <button className="btn btn-outline-success m-2" onClick={Undo}>Undo</button>
    <button className="btn btn-outline-success m-2" onClick={Redo}>Redo</button>
    <div className=""><button className="btn btn-danger m-2 " onClick={Export}> Export as PDF</button></div>
    
</div>
<div className='mb-3 p-4'>
<h2> Preview : </h2>
<p>{text}</p>
</div>
</div>

    </>
  )
}
