import React from 'react'
import "./start.css"
import canva2 from "./asstes/sapiens.svg";
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowRightLong } from '@fortawesome/free-solid-svg-icons/faArrowRightLong';


export default function Start() {
  return (
    <>
    <div className="pp">

    <div className="tt">
            <p className="t1">Welcome To <b className='change'><i></i></b></p>
            <div className='l1'>
           
               <Link className="btn btn-outline-success p-6" to="/Textform">Start <FontAwesomeIcon icon={faArrowRightLong} /></Link> 
        
        </div>
            
        </div>
        <div className="img">
      <img src={canva2} alt="" />
        </div>
       
    </div>
    </>
  )
}
